import { Component, OnInit, ViewChild } from '@angular/core';

// Ionic
import { IonSlides, NavController, LoadingController, Platform } from '@ionic/angular';

// Services
import { StorageService } from '../../services/storage.service';
import { DatabaseService } from '../../services/database.service';
import { AngularFireStorage } from '@angular/fire/storage';
import { OneSignal } from '@ionic-native/onesignal/ngx';

import { finalize } from 'rxjs/operators';

@Component({
  selector: 'app-select-plan',
  templateUrl: './select-plan.page.html',
  styleUrls: ['./select-plan.page.scss'],
})
export class SelectPlanPage implements OnInit {
  @ViewChild(IonSlides) slides: IonSlides;
  slideOpts = {
    effect: 'flip',
    zoom: false
  };
  segment_value: string = "free";
  button_text: string = "Adelante";
  constructor(private loadingController: LoadingController,
              private storage: StorageService,
              private af_storage: AngularFireStorage,
              private database: DatabaseService,
              private oneSignal: OneSignal,
              private platform: Platform,
              private navCtrl: NavController) { }

  ngOnInit() {
  }

  segmentChanged (val: any) {
    if (val === 'free') {
      this.slides.slideTo (0);
    } else if (val === 'standar') {
      this.slides.slideTo (1);
    } else {
      this.slides.slideTo (2);
    }
  }

  ionSlideDidChange () {
    this.slides.getActiveIndex ().then (val => {
      if (val === 0) {
        this.segment_value = 'free';
        this.button_text = "Adelante";
      } else if (val === 1) {
        this.segment_value = 'standar';
        this.button_text = "Escoger metodo de pago";
      } else {
        this.segment_value = 'premium';
        this.button_text = "Escoger metodo de pago";
      }
    })
  }

  navigateBack () {
    this.navCtrl.navigateBack ('service-first-image');
  }

  async createSoyProAccount () {
    const loading = await this.loadingController.create({
      message: 'Procesando Informacion ...'
    });

    await loading.present ();

    let soypro_data: any = JSON.parse (await this.storage.getObject('soypro_data'));
    let phone_number_valid: any = JSON.parse (await this.storage.getObject ('phone_number_valid'));
    let soypro_service_config: any = JSON.parse (await this.storage.getObject ('soypro_service_config'));
    
    let soypro_avatar: string = await this.storage.getValue ('soypro_avatar');
    let soypro_service_image: string = await this.storage.getValue ('soypro_service_image');
    let user_uid = await this.storage.getValue ('user_uid');  
     
    console.log ('soypro_data', soypro_data);
    console.log ('phone_number_valid', phone_number_valid);
    console.log ('soypro_service_config', soypro_service_config);

    console.log ('soypro_avatar', soypro_avatar);
    console.log ('soypro_service_image', soypro_service_image);
    console.log ('user_uid', user_uid);

    // Upload avatar to Storage
    const filePath = user_uid + '/avatar.jpg';
    const storageRef = this.af_storage.ref (filePath);
    const uploadTask = storageRef.putString (soypro_avatar, 'data_url');
    
    uploadTask.snapshotChanges ().pipe (
      finalize(() => {
        storageRef.getDownloadURL ().subscribe (async (avatar_url: any) => {
          await this.database.addProfessionals (user_uid, soypro_data, 
              soypro_service_config, phone_number_valid, avatar_url)
            .then (() => {
              if (this.platform.is ('cordova')) {
                this.oneSignal.sendTag (soypro_service_config.subcategory_id, "true");
                this.oneSignal.sendTag (soypro_service_config.service.id, "true");
              }
              
              if (soypro_service_image != null && soypro_service_image && undefined && soypro_service_image != '') {
                this.upload_image_service (user_uid,
                  soypro_service_config.service.id, 
                  soypro_service_image,
                  loading);
              } else {
                loading.dismiss ();
                this.navCtrl.navigateRoot ('soypro-completed');
              }
            }, error => {
              loading.dismiss ();
              console.log ("addProfessionals", error);
            })
          
        });
      })
    ).subscribe ();
  }

  upload_image_service (user_uid: string, service_id: string, image: string, loading: any) {
    const image_id = this.database.createId ();

    const filePath = user_uid + '/'+ service_id + '/' + image_id + '.jpg';
    const storageRef = this.af_storage.ref (filePath);
    const uploadTask = storageRef.putString (image, 'data_url');

    uploadTask.snapshotChanges ().pipe (
      finalize(async () => {
        await this.database.addImageService (user_uid, service_id, image, image_id, filePath)
          .then (() => {
            loading.dismiss ();
            this.navCtrl.navigateRoot ('soypro-completed');
          }, error => {
            loading.dismiss ();
            console.log ("Error upload_image_service, ", error);
          });
      })
    ).subscribe ();
  }

  selectPlan () {
    this.slides.getActiveIndex ().then (val => {
      if (val === 0) {
        this.createSoyProAccount ();
      } else {

      }
    });
  }
}
